<template>
    <section>
        <!---->
        <el-col :span="24" class="toolbar">
            <el-form :inline="true" :model="filters">
                <el-form-item label="">
                    <el-input v-model="filters.name" placeholder=""></el-input>
                </el-form-item>
                <el-form-item label="">
                    <el-input v-model="filters.code" placeholder=""></el-input>
                </el-form-item>
                <el-form-item>
                    <el-button type="primary" v-on:click="getUsers"></el-button>
                </el-form-item>
                <el-form-item>
                    <el-button type="primary" @click="handleAdd"></el-button>
                </el-form-item>
            </el-form>
        </el-col>
    
        <!---->
        <template>
            <el-table :data="users" highlight-current-row v-loading="listLoading" style="width: 100%;">
                <el-table-column type="index" width="60">
                </el-table-column>
                <el-table-column prop="code" label="" width="120" sortable>
                </el-table-column>
                <el-table-column prop="name" label="" width="120" sortable>
                </el-table-column>
                <el-table-column prop="address" label="" width="120" sortable>
                </el-table-column>
                <el-table-column prop="phone" label="" width="180" sortable>
                </el-table-column>
                <el-table-column prop="link_name" label="" width="180"  sortable>
                </el-table-column>
                <el-table-column prop="link_tel" label="" width="180"  sortable>
                </el-table-column>
                <el-table-column inline-template :context="_self" label="" min-width="320">
                    <span>
                        <el-button size="small" @click="handleEdit(row)"></el-button>
                        <!--<el-button type="danger" size="small" @click="handleDel(row)"></el-button>-->
                    </span>
                </el-table-column>
            </el-table>
        </template>
    
        <!---->
		<el-col :span="24" class="toolbar" style="padding-bottom:10px;">
			<el-pagination layout="total,sizes,prev, pager, next" @size-change="handleSizeChange" @current-change="handleCurrentChange" :page-sizes="[10, 200, 300, 400]" :page-size="pagesize" :total="total" style="float:right;">
			</el-pagination>
		</el-col>
    
        <!---->
        <el-dialog :title="editFormTtile" v-model="editFormVisible" :close-on-click-modal="false">
            <el-form :model="editForm" label-width="100px" :rules="editFormRules" ref="editForm">
    
                <el-form-item label="" prop="name">
                    <el-input v-model="editForm.name" auto-complete="off"></el-input>
                </el-form-item>
                <el-form-item label="" prop="name">
                    <el-input v-model="editForm.name" auto-complete="off"></el-input>
                </el-form-item>
                <el-upload class="upload-demo" drag action="" multiple>
                    <i class="el-icon-upload"></i>
                    <div class="el-upload__text">
                        <em></em>
                    </div>
                    <div class="el-upload__tip" slot="tip">jpg/png500kb</div>
                </el-upload>
                <el-form-item label="">
                    <el-input type="textarea" v-model="editForm.desc"></el-input>
                </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button @click.native="editFormVisible = false"> </el-button>
                <el-button type="primary" @click.native="editSubmit" :loading="editLoading">{{btnEditText}}</el-button>
            </div>
        </el-dialog>
    </section>
</template>

<script>
import util from '../../common/util'
import NProgress from 'nprogress'
import request, { getUserListPage, removeUser, editUser, addUser } from 'api';
import config from 'config';

export default {
	data() {
		return {
			filters: {
				name: '',
				code:'',
				page:1
			},
			users: [],
			total: 0,
			page: 1,
			pagesize: 10,
			listLoading: false,
			editFormVisible: false,//
			editFormTtile: '',//
			shopdetailVisible: false,
			shopdetailtitle: '',
			shopdetailobj:{

			},
			//
			editForm: {
				id: 0,
				name: '',
				sex: -1,
				age: 0,
				birth: '',
				addr: ''
			},
			editLoading: false,
			btnEditText: ' ',
			editFormRules: {
				name: [
					{ required: true, message: '', trigger: 'blur' }
				]
			}

		}
	},
	methods: {
		//
		formatSex: function (row, column) {
			return row.sex == 1 ? '' : row.sex == 0 ? '' : '';
		},
		handleCurrentChange(val) {
			this.page = val;
			this.filters.page = val;
			this.getUsers();
		},
		handleSizeChange(val) {
			console.log(` ${val} `);
		},
		//
		getUsers() {

			let para = {
				page: this.page,
				name: this.filters.name
			};
			this.listLoading = true;
			NProgress.start();
			// getUserListPage(para).then((res) => {
			// 	this.total = res.data.total;
			// 	this.users = res.data.users;
			// 	this.listLoading = false;
			// 	NProgress.done();
			// });
			request.get(config.api.goods.supplierindex,this.filters)
				.then((res) => {
					this.listLoading = false;
					NProgress.done();
					let { message, code, data } = res;
					if (code !== 200) {
						this.$notify({
							title: '',
							message: message,
							type: 'error'
						});
					} else {
						this.total = data.cnt.total;
						this.users = data.cnt.data;
						this.pagesize = data.cnt.per_page || 10;
					}
				})
		},
		//
		handleDel: function (row) {
			//console.log(row);
			var _this = this;
			this.$confirm('?', '', {
				//type: 'warning'
			}).then(() => {
				_this.listLoading = true;
				NProgress.start();
				let para = { id: row.id };
				removeUser(para).then((res) => {
					_this.listLoading = false;
					NProgress.done();
					_this.$notify({
						title: '',
						message: '',
						type: 'success'
					});
					_this.getUsers();
				});

			}).catch(() => {

			});
		},
		showshopdetail(row) {
			this.shopdetailVisible = true;
			request.get(config.api.shop.shopqueuedetail,{shop_id:row.id})
				.then((res) => {
					let { message, code, data } = res;
					if (code !== 200) {
						this.$notify({
							title: '',
							message: message,
							type: 'error'
						});
					} else {
						this.shopdetailobj=data.cnt;
					}
				})
		},
		//
		showorder(row){
			this.$router.push('/shoporder');
		},
		//
		handleEdit: function (row) {
			// this.editFormVisible = true;
			// this.editFormTtile = '';
			// this.editForm.id = row.id;
			// this.editForm.name = row.name;
			// this.editForm.sex = row.sex;
			// this.editForm.age = row.age;
			// this.editForm.birth = row.birth;
			// this.editForm.addr = row.addr;

			this.$router.push('/addsupplier/'+row.id);

		},
		// or 
		editSubmit: function () {
			var _this = this;

			_this.$refs.editForm.validate((valid) => {
				if (valid) {

					_this.$confirm('', '', {}).then(() => {
						_this.editLoading = true;
						NProgress.start();
						_this.btnEditText = '';

						if (_this.editForm.id == 0) {
							//
							let para = {
								name: _this.editForm.name,
								sex: _this.editForm.sex,
								age: _this.editForm.age,
								birth: _this.editForm.birth == '' ? '' : util.formatDate.format(new Date(_this.editForm.birth), 'yyyy-MM-dd'),
								addr: _this.editForm.addr,
							};
							addUser(para).then((res) => {
								_this.editLoading = false;
								NProgress.done();
								_this.btnEditText = ' ';
								_this.$notify({
									title: '',
									message: '',
									type: 'success'
								});
								_this.editFormVisible = false;
								_this.getUsers();
							});
						} else {
							//
							let para = {
								id: _this.editForm.id,
								name: _this.editForm.name,
								sex: _this.editForm.sex,
								age: _this.editForm.age,
								birth: _this.editForm.birth == '' ? '' : util.formatDate.format(new Date(_this.editForm.birth), 'yyyy-MM-dd'),
								addr: _this.editForm.addr,
							};
							editUser(para).then((res) => {
								_this.editLoading = false;
								NProgress.done();
								_this.btnEditText = ' ';
								_this.$notify({
									title: '',
									message: '',
									type: 'success'
								});
								_this.editFormVisible = false;
								_this.getUsers();
							});

						}

					});

				}
			});

		},
		//
		handleAdd: function () {
			// var _this = this;

			// this.editFormVisible = true;
			// this.editFormTtile = '';

			// this.editForm.id = 0;
			// this.editForm.name = '';
			// this.editForm.sex = 1;
			// this.editForm.age = 0;
			// this.editForm.birth = '';
			// this.editForm.addr = '';
			this.$router.push('/addsupplier/0');
		}
	},
	mounted() {
		this.getUsers();
	}
}
</script>

<style scoped>
.el-card__header {
    background-color: #E4E4E4
}

.clearfix:before,
.clearfix:after {
    display: table;
    content: "";
}

.clearfix:after {
    clear: both
}
</style>


